*{
  margin:0;
  padding:0;
  box-sizing:border-box;
  font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.navigation{
  width:95%;
  padding:2%;
  margin:0 auto;
  justify-content:space-between;
  font-size:20px;
  border:5px solid darkgreen;
  border-radius:20px;
}

.navigation nav{
  width:45%;
  font-size:23px;
  float:right;
  display:flex;
  margin-top:-3%;
  justify-content:space-between;
}

.navigation nav .navLinks{
  color:black;
  text-decoration: none;
  font-weight:bolder;
  transition:opacity 0.5 ease-in;
  margin-right:5%;
}
.navigation nav .navLinks:hover{
  opacity:0.5;
}
.hamburger-icon{
  display:none;
  float:right;
}

/*Navbar responsive code*/
@media only screen and (max-width:1000px){
  .hamburger-icon{
      display:block;
      font-size:30px;
      margin-right:3%;
      cursor:pointer;
      margin-top:-9vw;
  }
  .navigation nav{
      display:none;
      width:100%;
      float:none;
      margin-top:2%;
      flex-direction:column;
      text-align:center;
      border:4px solid black;
      border-radius:20px;
  }
  .navigation nav .navLinks{
      width:100%;
      margin-right:0;
      margin-bottom:5%;
      margin-top:3%;
  }
  .navigation .visible{
      display:flex;
  }
}